#schedule {
    background-image: url(https://img.freepik.com/free-photo/graphic-2d-colorful-wallpaper-with-grainy-gradients_23-2151001633.jpg);
    background-size:cover;
}
#details {
    background:linear-gradient(240deg,rgba(2, 1, 23, 0.592),rgba(1, 0, 19, 0.678)),url(/Images/gcu.jpg);
    background-size: cover;
    background-repeat: no-repeat;
 
    /* background-attachment: fixed; */
    /* height: 140vh; */
    min-width: 100%;

    
}

@media (orientation: portrait) and (max-width: 1020px) {
    #details {
        background:linear-gradient(240deg,rgba(2, 1, 23, 0.592),rgba(1, 0, 19, 0.678)),url(/Images/gcu.jpg);
        background-size: cover;
        background-repeat: no-repeat;
        height: auto;
        /* min-width: 100%; */
        background-position: 50%;
    
        
    }
}
@media (orientation: landscape) and (max-width: 1020px) {
    #details {
        background:linear-gradient(240deg,rgba(2, 1, 23, 0.592),rgba(1, 0, 19, 0.678)),url(/Images/gcu.jpg);
        background-size: cover;
        background-repeat: no-repeat;
     
        /* background-attachment: fixed; */
        height: auto ;
        min-width: 100%;
    
        
    }
    #schedule {
        background-image: url(https://img.freepik.com/free-photo/graphic-2d-colorful-wallpaper-with-grainy-gradients_23-2151001633.jpg);
        background-size:cover;
        margin-top: -12vh;
    }
}