#Ari{
  display: none;
}

#m {
  display: none;
}

.checkbtn {
  font-size: 30px;
  color: white;
  background: none;
  border: none;
  position: fixed;
  top: 20px;
  right: 20px;
  z-index: 1000;
  cursor: pointer;
  display: none;
}

#check {
  display: none;
}
nav {
  color: white;
  padding: 0px 20px 1px 1%;
  width: 100vw;
  z-index: 99999999;
  text-align: center;
  position: fixed;
  background-color: transparent; 
  overflow: hidden; 
}

nav::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(240deg,rgba(4, 4, 4, 0.163),rgba(247, 245, 245, 0.163));
  filter: blur(5px); 
  z-index: -1; 
}
nav ul {
  font-size: large;
  list-style: none;
  margin-top: 0;
  box-sizing: border-box;
  font-weight: bolder;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  display: inline;
}

nav li {
  padding: 1% 1%;;
  display: inline-block;
}

nav a {
  position: relative;
  text-decoration: none;
  display: inline-block;
  padding: 0.5em 1em;
  color: aliceblue;
  z-index: 1;
  background-color: black;
  border-radius: 10%;
  box-shadow: 10px 0px 20px rgb(241, 5, 5);
  border: 2px solid;
  border-image: linear-gradient(240deg, rgb(233, 141, 3), rgb(255, 0, 0)) 1;
  transition: all 0.3s ease;
}

nav a:hover {
  box-shadow: 10px 0px 20px rgb(11, 212, 234);
  border-image: linear-gradient(240deg, rgb(9, 228, 206), rgb(42, 18, 193)) 1;
}
nav a:focus {
  box-shadow: 10px 0px 20px rgb(11, 212, 234);
  border-image: linear-gradient(240deg, rgb(9, 228, 206), rgb(42, 18, 193)) 1;
}

@media (orientation: portrait) and (max-width: 1020px) {
  #m {
    display: block;
    background-color: rgb(125, 124, 124);
    border-radius: 0%;
    position: relative;
    color: white;
    font-weight: bold;
    padding: 10% 10%;
    margin-left: -35vw;
    
    margin-top: -1vh;
    width: 100vw;
    
    
  }
  nav {
    text-align: center;
    
  }
  #check:checked ~ ul {
    margin-left: -1%;
    padding: 0% 30%;
    background: linear-gradient(220deg, rgb(255, 255, 255), rgb(255, 255, 255));
    position: fixed;
    background-size: 30px;
    width: 100%;
    display: block;
    z-index: 99999;
    box-sizing: border-box;
    text-align: center;
    
  }

  #check:checked ~ .checkbtn .fa-bars {
    display: none;
  }
  .checkbtn .fa-times {
    display: none;
  }

  .checkbtn .fa-bars {
    display: block;
  }
  #check:checked ~ .checkbtn .fa-times {
    display: block;
  }

  .checkbtn {
    display: block;
    position: fixed;
    margin-left: 90%;
    background: linear-gradient(
      45deg,
      black,
      rgb(4, 4, 4),
      rgb(8, 237, 203),
      rgb(0, 0, 0),
      black
    );
    -webkit-background-clip: text;
    color: transparent;
    
    padding: 1%;
    
  }
  #check:not(:checked) ~ nav ul {
    display: none;
  }
  nav ul {
    margin-left: 30%;
    margin-top: 50%;
    padding: 0% 8% 8%;
    position: fixed;
    background-size: 30px 30px;
    display: none;
  }

  nav li {
    display: block;
    
  }

  nav a {
    text-decoration: none;
    border: none;
    color: rgb(10, 162, 124);
    background: none;
    border-radius: none;
    box-shadow: none;
    margin-top: 0%;
    margin-left: -0%;
    border-image: none;
    transition: all 0.5s;
    
  }
  a {
    margin-top: -1%;
  }
  nav a:hover {
    box-shadow: none;
  }

  nav span {
    display: none;
  }

 
  #Ari{
    display: block;
    border-radius: 0%;
    position: absolute;
    color: white;
    font-weight: bold;
    padding: 10%;
    margin-left: -37%;
    width: 100vw;
    height: 100vh;
    text-align: center;

    background: linear-gradient(
      220deg,
      rgba(32, 21, 64, 0.3),
      rgba(25, 54, 61, 0.817)
    );
  }
 
  
}
