.card {
  position: relative;
  margin-left: 15vw;
  margin-top: 0vh;
  width: 65vw;
  height: 29vh;
  background-color: #000;
  display: flex;
  flex-direction: column;
  justify-content: start; 
  padding: 12px;
  justify-content: space-between;
  border-radius: 8px;
  box-shadow: 10px 10px 40px rgb(110, 109, 109);
  color: white;
  height: auto;
  text-align: left;
}

.heading {
  font-size: 20px;
  font-weight: 700;
  margin-top: 10%;
  
}
.Text{
  /* text-align: center; */
  margin-top: -22%;
  margin-left: 35%;
}



.card p:last-child {
  color: #e81cff;
  font-weight: 600;
  position: relative;
  
  background-color: white;
  padding: 2vh; 
  border-radius: 25px;
  max-width: 300px;
  text-align: center;
  margin-top: 10vh;
 
  

}
.card img{
  border-radius: 25px;
  height: 30vh;
  object-position:  100% 0;
  max-width: 30%;
  object-fit: cover;
}
#animate{
  animation: cardAnimate 3s both;
}
@keyframes cardAnimate {
  from{
    opacity: 0;
    transform: translateY(200px)scale(0.3);

  }
  to{
    opacity: 1;
    transform: translateY(0)scale(1);
  }
}

  
  @media(orientation: portrait) and (max-width: 1020px) {
    .card {
        position: relative;
        margin-left: 15vw;
        margin-top: 5%;
        width: 65vw;
        height: 50vh;
        background-color: #000;
        display: flex;
        flex-direction: column;
        justify-content: start;
        padding: 12px;
        gap: 12px;
        border-radius: 8px;
        box-shadow: 10px 10px 40px rgb(110, 109, 109);
        color: white;
        height: auto;
        text-align: center;
      }
      #animate{
        animation:textanimation both;
    animation-timeline:view(90% 0%) ;
}
@keyframes textanimation{
    from{
        opacity: 0;
        transform: translateY(0) scale(0);
    }
    to{
        opacity: 1;
        transform: translateY(0) scale(1);
    }
}
      
      .heading {
        font-size: 20px;
        text-transform: capitalize;
        font-weight: 700;
        margin-top: none;
      }
      
      .card p:not(.heading) {
        font-size: 14px;
        max-width: auto;
        margin-top: auto;
        justify-content: none;
      }
      
      .card p:last-child {
        color: #e81cff;
        font-weight: 600;
        margin: auto;
        position: relative;
        /* margin-top: none;  */
        

      }
      .card img{
        border-radius: 25px;
        height: 25vh;
        max-width: none;
        object-fit: cover;
      }
