@media (orientation: portrait) and (max-width: 1020px) {

    #Title {
        font-size: 2.0rem;
        /* padding-top: 5%; */
        font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
        
        display: block;
    }
    #heading{
        padding-top: 5%;
        margin-top: 3vh;
    }
    #bo hr{
        display: block;
    }
    

}
@media (orientation: landscape) and (max-width: 1020px) {
    #heading{
        font-size: 4rem;
        font-family:'Courier New', Courier, monospace;
       text-shadow: 10px 0px 40px rgb(147, 44, 44);
       background: linear-gradient(240deg,rgb(211, 56, 21),rgb(248, 11, 11));
       color: transparent;
       -webkit-background-clip:text;
       position: relative;
        margin-top: 0%;
        padding-top: 10%;
    
         
    }
    #Title {
        font-size: 2.0rem;

        font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;

        display: block;
    }
    hr{
        display: block;
    }
 
}
