:root {
    --color-1: #1488ed;
    --color-2: #2a9a9f;
    --color-3: #d92815;
    --color-4: #f3d80b;
    --color-5: #ebad12;
  }
  
#about{
    margin-top: -8.2vh;
    color:wheat;
}
#about h1 {
    font-size: 10vh;
    text-align: center;
    padding: 5% 0% 0 0;
    background: linear-gradient(
        220deg, var(--color-1) 19%,
        transparent 19%, transparent 20%,
        var(--color-2) 20%, var(--color-2) 39%,
        transparent 39%, transparent 40%,
        var(--color-3) 40%, var(--color-3) 59%,
        transparent 59%,transparent 60%,
        var(--color-4) 60%, var(--color-4) 79%,
        transparent 79%, transparent 80%, 
        var(--color-5) 80%
      );
    color: transparent;
    -webkit-background-clip: text;
}
#about h3{
    text-align: center;
    margin-top:-8vh;
    font-weight: 900;

}
#about h5{
    margin: 6vw;
    font-size: 2rem;
    max-width: auto;
}
@media(orientation: portrait) and (max-width: 1020px) {
    #about{
        color:wheat;
        margin-top: -3.9vh;
    }
    #about h1 {
        font-size: 5vh;
        text-align: center;
        padding: 5% 0% 0 0;
        background: linear-gradient(
            220deg, var(--color-1) 19%,
            transparent 19%, transparent 20%,
            var(--color-2) 20%, var(--color-2) 39%,
            transparent 39%, transparent 40%,
            var(--color-3) 40%, var(--color-3) 59%,
            transparent 59%,transparent 60%,
            var(--color-4) 60%, var(--color-4) 79%,
            transparent 79%, transparent 80%, 
            var(--color-5) 80%
          );
        color: transparent;
        -webkit-background-clip: text;
    }
    #about h3{
        text-align: center;
        font-size: -3rem;
        /* margin-top:-8vh; */
        font-weight: 900;
        display: hidden;
        opacity: 0;
    }
    #about h5{
        margin: 6vw;
        font-size: 1rem;
        max-width: auto;
    }
}