
:root {
    --color-1: #072f55;
    --color-2: #2a9a9f;
    --color-3: #f98d00;
    --color-4: #e83611;
    --color-5: #f1b211;
  }
  .zoomin {
    animation: zoomin linear;
    animation-timeline: view();
    
  }

#bg{
    background-image: url(/Images/mainbg1.jpeg);
    background-repeat: no-repeat;
    background-size: cover;
    width: auto;
    height: 100vh;
    background-position: 0 70%;

}
@keyframes zoomin {
    from{
        opacity: 1;
        scale: 0.5;
    }
    to{
        opacity:0;
        scale: 1.5;
    }

}
#frame {
    background-image: url(/Images/frame.png);
    height: 100vh;
    width: 100%;
    position: relative;
    background-repeat: no-repeat;
    background-size:cover;
    mix-blend-mode:darken;
    
}
.text{
    font-size: large;
    font-weight:900;
    transform: scale(3);
    position: relative;
    text-transform: uppercase;
    background: linear-gradient(
      220deg, var(--color-1) 19%,
      transparent 19%, transparent 20%,
      var(--color-2) 20%, var(--color-2) 39%,
      transparent 39%, transparent 40%,
      var(--color-3) 40%, var(--color-3) 59%,
      transparent 59%,transparent 60%,
      var(--color-4) 60%, var(--color-4) 79%,
      transparent 79%, transparent 80%, 
      var(--color-5) 80%
    );
    -webkit-background-clip: text;
        color:transparent;
        margin-left:50%;
        margin-top:-30%;
        position:absolute;
}
.textappear{
    animation:textanimation both;
    animation-timeline:view(40% 5%) ;
}
@keyframes textanimation{
    from{
        opacity: 0;
        transform: translateY(200px) scale(0);
    }
    to{
        opacity: 1;
        transform: translateY(0) scale(1);
    }
}


@media (orientation: portrait) and (max-width: 1020px) {
    :root {
        --color-1: #072f55;
        --color-2: #2a9a9f;
        --color-3: #34f006;
        --color-4: #903523;
        --color-5: #f1b41c;
      }
    #bg{
        background-image: url(/Images/mainbg.gif);
        background-repeat: no-repeat;
        background-position: -2% 57.5%;
        zoom: 0.1;
       /* display: block; */
    
    }
    .textappear{
        animation: none;
    }
    .zoomin{
        animation: none;
    }
    #frame {
        display:none;
    

    }
    .text{
            display: none;
    }
    

}
@media (orientation: landscape) and (max-width: 1020px) {
    :root {
        --color-1: #072f55;
        --color-2: #2a9a9f;
        --color-3: #f98d00;
        --color-4: #e83611;
        --color-5: #f1b211;
      }
      
    
    #bg{
        background-image: url(/Images/mainbg1.jpeg);
        background-repeat: no-repeat;
        background-size: cover;
        width: 100%;
        height: 100vh;
        background-position: 0 70%;
    
    }
    .zoomin{
        animation: none;
    }
    #frame {
        background-image: url(/Images/frame.png);
        height: 100vh;
        width: 100%;
        position: relative;
        background-repeat: no-repeat;
        background-size:cover;
        mix-blend-mode:darken;
        
    }
    .text{
        font-size: 2.5vh;
        font-weight:900;
        transform: scale(3);
        position: relative;
        text-transform: uppercase;
        background: linear-gradient(
          220deg, var(--color-1) 19%,
          transparent 19%, transparent 20%,
          var(--color-2) 20%, var(--color-2) 39%,
          transparent 39%, transparent 40%,
          var(--color-3) 40%, var(--color-3) 59%,
          transparent 59%,transparent 60%,
          var(--color-4) 60%, var(--color-4) 79%,
          transparent 79%, transparent 80%, 
          var(--color-5) 80%
        );
        -webkit-background-clip: text;
            color:transparent;
            margin-left:53%;
            margin-top:-25%;
            position:absolute;
    }
    
    

}