#location {
    text-align: center;
    background-image: url(https://img.freepik.com/premium-photo/rain-overlays-realistic-rain-rainfall-rainy-weather_487474-2035.jpg);
    color: white;
    background-attachment: fixed;
    margin-top: -4.1vh;
}

#location h1 {
    padding: 6% 0 0 0;
    font-size: 3rem;
}

#location iframe {
    width: 80%;
    height: 80vh;
}

#location .address {
    margin-top: 3%;
}

#location .address h2 {
    font-size: 3rem;
}

#location .contact h2 {
    font-size: 3rem;
}

/* Flexbox to center the contact content */
#location .contact #con {
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1.5rem;  /* Adjust the gap between the items */
    font-size: 1.5rem;
    margin: 0;
}

/* Styling for individual contact paragraphs */
#location .contact #con p {
    border-right: 2px solid white;
    border-left: 2px solid white;
    padding: 3%;
    font-size: 1.5rem;
    margin: 0;  /* Remove negative margins */
    text-align: center;
}

/* Styling for location address */
#location .address #loc {
    flex-direction: row;
    display: inline-flex;
    gap: auto;
}

#location .address #loc p {
    border-right: 2px solid white;
    border-left: 2px solid white;
    padding: 2%;
    text-align: center;
}

@media (orientation: portrait) and (max-width: 1020px) {
    #location iframe {
        width: 80%;
        height: 40vh;
    }

    #location .address #loc p {
        border-right: 2px solid white;
        border-left: 2px solid white;
        padding: 1%;
        text-align: center;
        transform: scale(0.8);
    }

    #location .contact #con p {
        border-right: 2px solid white;
        border-left: 2px solid white;
        padding: 3%;
        text-align: center;
        transform: scale(0.6);
    }

    /* Centering content in portrait mode */
    #location .contact #con {
        font-size: 1.5rem;
        align-items: center;  /* Ensure it is centered horizontally */
        justify-content: center;  /* Center the content vertically */
    }
}
